import React from "react";
import styled from "styled-components";

const StyledButton = styled.a`
  background-color: ${props => props.bgColor};
  display: flex;
  justify-content: ${props => props.textAlign};
  align-items: center;
  flex-wrap: wrap;
  cursor: pointer;
  width: ${props => props.btnWidth}px;
  height: ${props => props.btnHeight}px;
  border: ${props => props.borderSize}px ${props => props.borderStyle} ${props => props.borderColor};
  border-radius: ${props => props.borderRadius}px;
  font-size: ${props => props.btnFontSize}px;
  color: ${props => props.fontColor};
  text-decoration: none;
  padding: ${props => props.btnPadding}px;
  margin: ${props => props.btnMargin}px;
  font-weight: ${props => props.fontWeigth};
  transition: all ${prop=>prop.transitionTime}ms ease-in-out;

  &:hover {
    color: ${props => props.hoverColor};
    background-color: ${props => props.hoverBgColor};
    border: ${props => props.hoverBorderSize}px ${props => props.hoverBorderStyle} ${props => props.hoverBorderColor};
  }
`;

export default function Button(props) {

  const {
    text,
    btnFontSize,
    btnHeight,
    btnWidth,
    bgColor,
    borderSize,
    borderStyle,
    borderColor,
    fontColor,
    borderRadius,
    hoverColor,
    hoverBgColor,
    hoverBorderColor,
    url,
    btnMargin,
    btnPadding,
    textAlign,
    transitionTime,
    isURL,
    isHover,
    isBorder,
    hoverBorderSize,
    hoverBorderStyle
  } = props;
  return (
    <StyledButton
      btnFontSize={btnFontSize}
      textAlign={textAlign}
      href={isURL ? 'https://'+url : url} 
      
      btnMargin={btnMargin}
      btnPadding={btnPadding}
      btnWidth={btnWidth}
      btnHeight={btnHeight}

      bgColor={bgColor}
      fontColor={fontColor}

      borderSize={isBorder ? borderSize : '0'}
      borderStyle={isBorder ? borderStyle : 'solid'}
      borderColor={isBorder ? borderColor : bgColor}
      borderRadius={borderRadius}

      hoverColor={isHover ? hoverColor : ''}
      hoverBgColor={isHover ? hoverBgColor : ''}
      hoverBorderColor={isBorder ? hoverBorderColor : ''}
      transitionTime={isHover ? transitionTime : '0'}
      hoverBorderSize={isBorder ? (isHover ? hoverBorderSize : '') : ('')}
      hoverBorderStyle={isBorder ? (isHover ? hoverBorderStyle : '') : ('')}

      onClick={isURL ?  '' : (e)=>{e.preventDefault()}}
      target="_blank"
    >
      {text}
    </StyledButton>
  );
}
