import React, { useState } from "react";
import styled from "styled-components";
import { ChromePicker } from "react-color";

const StyledMenu = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  input {
    width: 300px;
    outline: none;
    border: none;
    margin: 2px;
  }

  input[type="checkbox"] {
    margin: 5px;
    width: 20px;
  }

  select {
    width: 300px;
    margin: 2px;
    border: none;
  }
`;

const MenuNavbar = styled.div`
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: lightblue;
  z-index: 2;
`;

const MenuButton = styled.div`
  padding: 5px;
  background-color: lightblue;
  cursor: pointer;
  border-right: 1px solid lightseagreen;

  &:hover {
    background-color: orange;
  }
`;

const PickerButton = styled.div`
  background-color: lightblue;
  width: 310px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 5px;

  &:hover {
    background-color: orange;
  }
`;

const MenuSidebar = styled.div`
  position: relative;
  padding: 25px;
  top: 12px;
  display: flex;
  flex-direction: column;
  height: fit-content;
  background-color: aliceblue;
  min-height: fit-content;
  width: 300px;
  border-bottom: 1px solid lightblue;
`;

const PickerContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
`;

export default function Menu(props) {
  const {
    text,
    onTextChange,
    btnFontSize,
    onFontSizeChange,
    btnHeight,
    btnWidth,
    onHeightChange,
    onWidthChange,
    bgColor,
    setBgColor,
    borderSize,
    onBorderSizeChange,
    borderStyle,
    onBorderStyleChange,
    borderColor,
    setBorderColor,
    fontColor,
    setFontColor,
    borderRadius,
    setBorderRadius,
    hoverColor,
    setHoverColor,
    hoverBgColor,
    setHoverBgColor,
    hoverBorderColor,
    setHoverBorderColor,
    url,
    onUrlChange,
    btnMargin,
    setBtnMargin,
    btnPadding,
    setBtnPadding,
    textAlign,
    onTextAlignChange,
    transitionTime,
    onTransitionTimeChange,
    isURL,
    onIsURLChange,
    isHover,
    onIsHoverChange,
    isBorder,
    onIsBorderChange,
    hoverBorderStyle,
    hoverBorderSize,
    setHoverBorderStyle,
    setHoverBorderSize
  } = props;
  const [showBgPicker, setShowBgPicker] = useState(false);
  const [showBorderPicker, setShowBorderPicker] = useState(false);
  const [showFontColorPicker, setShowFontColorPicker] = useState(false);
  const [showHoverColor, setShowHoverColor] = useState(false);
  const [showHoverBgColor, setShowHoverBgColor] = useState(false);
  const [showHoverBorderColor, setShowHoverBorderColor] = useState(false);

  const [isTextSection, setIsTextSection] = useState(true);
  const [isSizeSection, setIsSizeSection] = useState(false);
  const [isBorderSection, setisBorderSection] = useState(false);
  const [isHoverSection, setIsHoverSection] = useState(false);
  const [isColorSection, setIsColorSection] = useState(false);

  return (
    <>
      <MenuNavbar>
        <MenuButton
          onClick={() => {
            setIsTextSection(true);
            setIsSizeSection(false);
            setisBorderSection(false);
            setIsHoverSection(false);
            setIsColorSection(false);
          }}
        >
          Text
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsTextSection(false);
            setIsSizeSection(true);
            setisBorderSection(false);
            setIsHoverSection(false);
            setIsColorSection(false);
          }}
        >
          Size
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsTextSection(false);
            setIsSizeSection(false);
            setisBorderSection(false);
            setIsHoverSection(false);
            setIsColorSection(true);
          }}
        >
          Color
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsTextSection(false);
            setIsSizeSection(false);
            setisBorderSection(true);
            setIsHoverSection(false);
            setIsColorSection(false);
          }}
        >
          Border
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsTextSection(false);
            setIsSizeSection(false);
            setisBorderSection(false);
            setIsHoverSection(true);
            setIsColorSection(false);
          }}
        >
          Hover
        </MenuButton>
      </MenuNavbar>
      <StyledMenu>
        {isTextSection && (
          <MenuSidebar>
            <label for="text">Text: </label>
            <input
              id="text"
              type="text"
              placeholder=""
              autoComplete="off"
              maxLength="50"
              onChange={onTextChange}
              value={text}
            />
            <label for="fontSize">Font size: </label>
            <input
              id="fontSize"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="64"
              onChange={onFontSizeChange}
              value={btnFontSize}
            />
            <label for="textAlign">Text justify: </label>
            <select
              id="textAlign"
              value={textAlign}
              onChange={onTextAlignChange}
            >
              <option value="flex-start">left</option>
              <option value="center">center</option>
              <option value="flex-end">right</option>
            </select>
            <form>
              <label for="isURL">Add URL</label>
              <input
                id="isURL"
                type="checkbox"
                checked={isURL}
                onChange={onIsURLChange}
              />
            </form>
            {isURL && (
              <>
                <label for="url">Url: </label>
                <input
                  id="url"
                  type="text"
                  placeholder="https://"
                  autoComplete="off"
                  maxLength="100"
                  onChange={onUrlChange}
                  value={url}
                />
              </>
            )}
          </MenuSidebar>
        )}

        {isSizeSection && (
          <MenuSidebar>
            <label for="width">Width: </label>
            <input
              id="width"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="1200"
              onChange={onWidthChange}
              value={btnWidth}
            />
            <label for="height">Height: </label>
            <input
              id="height"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="450"
              onChange={onHeightChange}
              value={btnHeight}
            />
            <label for="padding">Padding: </label>
            <input
              id="padding"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="500"
              onChange={(e) => {
                setBtnPadding(e.target.value);
              }}
              value={btnPadding}
            />
            <label for="margin">Margin: </label>
            <input
              id="margin"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="500"
              onChange={(e) => {
                setBtnMargin(e.target.value);
              }}
              value={btnMargin}
            />
            <label for="border-radius">Border radius: </label>
            <input
              id="border-radius"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="180"
              onChange={(e) => {
                setBorderRadius(e.target.value);
              }}
              value={borderRadius}
            />
          </MenuSidebar>
        )}
        {isColorSection && (
          <MenuSidebar>
            <PickerButton
              onClick={() => {
                setShowFontColorPicker(!showFontColorPicker);
              }}
            >
              Font color
            </PickerButton>
            {showFontColorPicker && (
              <PickerContainer>
                <ChromePicker
                  color={fontColor}
                  onChange={(updatedColor) => setFontColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
            <PickerButton
              onClick={() => {
                setShowBgPicker(!showBgPicker);
              }}
            >
              Background color
            </PickerButton>
            {showBgPicker && (
              <PickerContainer>
                <ChromePicker
                  color={bgColor}
                  onChange={(updatedColor) => setBgColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
          </MenuSidebar>
        )}
        {isBorderSection && (
          <MenuSidebar>
            <form>
              <label for="isBorder">Enable border</label>
              <input
                id="isBorder"
                type="checkbox"
                checked={isBorder}
                onChange={onIsBorderChange}
              />
            </form>
            {isBorder && (
              <>
                <label for="border-size">Border size: </label>
                <input
                  id="border-size"
                  type="number"
                  placeholder=""
                  autoComplete="off"
                  min="0"
                  max="100"
                  onChange={onBorderSizeChange}
                  value={borderSize}
                />
                <label for="borderStyle">Border style: </label>
                <select
                  id="borderStyle"
                  value={borderStyle}
                  onChange={onBorderStyleChange}
                >
                  <option value="solid">solid</option>
                  <option value="dotted">dotted</option>
                  <option value="dashed">dashed</option>
                  <option value="double">double</option>
                </select>
                <PickerButton
                  onClick={() => {
                    setShowBorderPicker(!showBorderPicker);
                  }}
                >
                  Border color
                </PickerButton>
                {showBorderPicker && (
                  <PickerContainer>
                    <ChromePicker
                      color={borderColor}
                      onChange={(updatedColor) =>
                        setBorderColor(updatedColor.hex)
                      }
                    ></ChromePicker>
                  </PickerContainer>
                )}
              </>
            )}
          </MenuSidebar>
        )}
        {isHoverSection && (
          <MenuSidebar>
            <form>
              <label for="isHover">Enable hover effect</label>
              <input
                id="isHover"
                type="checkbox"
                checked={isHover}
                onChange={onIsHoverChange}
              />
            </form>
            {isHover && (
              <>
                <PickerButton
                  onClick={() => {
                    setShowHoverColor(!showHoverColor);
                  }}
                >
                  Hover font color
                </PickerButton>
                {showHoverColor && (
                  <PickerContainer>
                    <ChromePicker
                      color={hoverColor}
                      onChange={(updatedColor) =>
                        setHoverColor(updatedColor.hex)
                      }
                    ></ChromePicker>
                  </PickerContainer>
                )}
                <PickerButton
                  onClick={() => {
                    setShowHoverBgColor(!showHoverBgColor);
                  }}
                >
                  Hover background color
                </PickerButton>
                {showHoverBgColor && (
                  <PickerContainer>
                    <ChromePicker
                      color={hoverBgColor}
                      onChange={(updatedColor) =>
                        setHoverBgColor(updatedColor.hex)
                      }
                    ></ChromePicker>
                  </PickerContainer>
                )}
                {isBorder && (
                  <>
                  <PickerButton
                  onClick={() => {
                    setShowHoverBorderColor(!showHoverBorderColor);
                  }}
                >
                  Hover border color
                </PickerButton>
                {showHoverBorderColor && (
                  <PickerContainer>
                    <ChromePicker
                      color={hoverBorderColor}
                      onChange={(updatedColor) =>
                        setHoverBorderColor(updatedColor.hex)
                      }
                    ></ChromePicker>
                  </PickerContainer>
                )}
                <label for="hoverBorderSize">Hover border size: </label>
                <input
                  id="hoverBorderSize"
                  type="number"
                  placeholder=""
                  autoComplete="off"
                  min="0"
                  max="100"
                  onChange={(e)=>setHoverBorderSize(e.target.value)}
                  value={hoverBorderSize}
                />
                <label for="hoverBorderStyle">Hover border style: </label>
                <select
                  id="hoverBorderStyle"
                  value={hoverBorderStyle}
                  onChange={(e)=> setHoverBorderStyle(e.target.value)}
                >
                  <option value="solid">solid</option>
                  <option value="dotted">dotted</option>
                  <option value="dashed">dashed</option>
                  <option value="double">double</option>
                </select>
                </>
                )}
                
                <label for="transitionTime">Transition time: </label>
                <input
                  id="transitionTime"
                  type="number"
                  placeholder="in miliseconds"
                  autoComplete="off"
                  min="0"
                  max="5000"
                  onChange={onTransitionTimeChange}
                  value={transitionTime}
                />
              </>
            )}
          </MenuSidebar>
        )}
      </StyledMenu>
    </>
  );
}
