import React, {useState} from "react";
import styled from "styled-components";
import { CopyToClipboard } from "react-copy-to-clipboard";

const StyledClipboard = styled.div`
  height: 200px;
  width: 800px;
  background-color: aliceblue;
  border: 1px solid lightblue;
  margin-top: 10px;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 13px;
`;

const ClipboardMsg = styled.div`
background-color: orange;
margin: 5px;
padding: 5px;
font-size: 12px;
`

const ClipboardSwitch = styled.div`
width: 100%;
display: flex;
`

const ClipboardSwitchBtn = styled.div`
background-color: lightblue;
padding: 5px;
cursor: pointer;

&:hover {
  background-color: orange;
}
`

const ClipboardSection = styled.div`
  margin: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  h4 {
    margin: 1px;
  }
`;

const CopyButton = styled.div`
  height: 26px;
  width: 90%;
  background-color: lightblue;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  cursor: pointer;

  &:hover {
    background-color: orange;
  }
`;

const ClipboardText = styled.div`
  margin: 5px;
  text-align: center;
  background-color: white;
  border: 1px solid lightblue;
  font-size: 0.9rem;
`;

export default function Clipboard(props) {
  const [isCSS, setIsCSS] = useState(false)
  const [isHTML, setIsHTML] = useState(true)
  const [isCopied, setIsCopied] = useState(false)

  const onCopyText = () => {
    setIsCopied(true);
    setTimeout(() => {
      setIsCopied(false);
    }, 1000);
  };

  const {
    text,
    btnFontSize,
    btnHeight,
    btnWidth,
    bgColor,
    borderSize,
    borderStyle,
    borderColor,
    fontColor,
    borderRadius,
    hoverColor,
    hoverBgColor,
    hoverBorderColor,
    url,
    btnMargin,
    btnPadding,
    textAlign,
    transitionTime,
    isURL,
    isHover,
    isBorder,
    hoverBorderSize,
    hoverBorderStyle
  } = props;
  const clipboardTextHTMLPlain = `<a class="btn" ${
    isURL ? `href='https://${url}'` : ""
  }>${text}</a>`;
  const clipboardButtonCSS = `
    .btn {
        display: flex;
        justify-content: ${textAlign};
        align-items: center;
        font-size: ${btnFontSize}px;
        height: ${btnHeight}px;
        width: ${btnWidth}px;
        background-color: ${bgColor};
        ${
          isBorder
            ? `border: ${borderSize}px ${borderStyle} ${borderColor};`
            : ""
        }
        
        color: ${fontColor};
        border-radius: ${borderRadius}px;
        cursor: pointer;
        text-decoration: none;
        padding: ${btnPadding}px;
        margin: ${btnMargin}px;
        transition: all ${transitionTime}ms ease-in-out;
    }
    `;
  const clipboardHoverCss = `
    .btn:hover {
    color: ${hoverColor};
    background-color:${hoverBgColor};
    ${
      isBorder ? `border: ${hoverBorderSize}px ${hoverBorderStyle} ${hoverBorderColor};` : ""
    }
    
  }
  `;

  const clipboardAll = clipboardButtonCSS + clipboardHoverCss;

  return (
    <>
    
    <StyledClipboard>
      <ClipboardSwitch>
        <ClipboardSwitchBtn onClick={()=>{
          setIsHTML(true)
          setIsCSS(false)}}>
            HTML
        </ClipboardSwitchBtn>
        <ClipboardSwitchBtn onClick={()=>{
          setIsHTML(false)
          setIsCSS(true)}}>
          CSS
        </ClipboardSwitchBtn>
      </ClipboardSwitch>
      {isHTML && (
        <ClipboardSection>
        <h4>
          Copy code below and paste it into body section in your html file
        </h4>
        <ClipboardText>{clipboardTextHTMLPlain}</ClipboardText>
        <CopyToClipboard onCopy={onCopyText} text={clipboardTextHTMLPlain}>
          <CopyButton>Copy HTML</CopyButton>
        </CopyToClipboard>
      </ClipboardSection>
      )}
      {isCSS && (
        <ClipboardSection>
        <h4>Copy code below and paste it into your css file</h4>
        <ClipboardText>
          {clipboardButtonCSS}
          {isHover && clipboardHoverCss}
        </ClipboardText>
        <CopyToClipboard onCopy={onCopyText} text={isHover ? clipboardAll : clipboardButtonCSS}>
          <CopyButton>Copy CSS</CopyButton>
        </CopyToClipboard>
      </ClipboardSection>
      )}
      
    </StyledClipboard>
    {isCopied && 
    <ClipboardMsg>Copied!</ClipboardMsg>}
    </>
  );
}
