import React, { useState } from "react";
import styled from "styled-components";
import Menu from "./Menu";
import Button from "./Button";
import Clipboard from "./Clipboard";

const StyledApp = styled.div`
  display: flex;
`;

const AppTitle = styled.div`
position: absolute;
right: 0;
top: 0;
padding: 5px;
z-index: 3;
font-size: 17px;
font-weight: 700;
`

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: fit-content;
`;

const ButtonContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 99.99%;
  min-height: 600px;
  border-left: 1px solid lightblue;
  border-bottom: 1px solid lightblue;
`;

function App() {
  const [isURL, setIsURL] = useState(false)
  const [isHover, setIsHover] = useState(false)
  const [isBorder, setIsBorder] = useState(false)

  const [text, setText] = useState("sample button");
  const [btnFontSize, setBtnFontSize] = useState("18");
  const [fontColor, setFontColor] = useState("black");
  const [textAlign, setTextAlign] = useState("center")
  const [url, setUrl] = useState("");

  const [btnWidth, setWidthChange] = useState("200");
  const [btnHeight, setHeightChange] = useState("100");
  const [btnPadding, setBtnPadding] = useState("0")
  const [btnMargin, setBtnMargin] = useState("0")
  const [bgColor, setBgColor] = useState("#BBB");

  const [borderSize, setBorderSize] = useState("0");
  const [borderStyle, setBorderStyle] = useState("solid");
  const [borderColor, setBorderColor] = useState(bgColor);
  const [borderRadius, setBorderRadius] = useState("0");
  
  const [hoverColor, setHoverColor] = useState(fontColor);
  const [hoverBgColor, setHoverBgColor] = useState(bgColor);
  const [hoverBorderColor, setHoverBorderColor] = useState(borderColor);
  const [hoverBorderSize, setHoverBorderSize] = useState(borderSize);
  const [hoverBorderStyle, setHoverBorderStyle] = useState(borderStyle) 

  const [transitionTime, setTransitionTime] = useState("0")
  


  const onTextChange = (e) => {
    setText(e.target.value);
  };
  
  const onUrlChange = (e) => {
    setUrl(e.target.value);
  };

  const onTextAlignChange = (e) => {
    setTextAlign(e.target.value);
  };

  const onFontSizeChange = (e) => {
    setBtnFontSize(e.target.value);
  };

  const onHeightChange = (e) => {
    setHeightChange(e.target.value);
  };

  const onWidthChange = (e) => {
    setWidthChange(e.target.value);
  };

  const onBorderSizeChange = (e) => {
    setBorderSize(e.target.value);
  };

  const onBorderStyleChange = (e) => {
    setBorderStyle(e.target.value);
  };

  const onTransitionTimeChange = (e) => {
    setTransitionTime(e.target.value)
  }

  const onIsURLChange = () => {
    setIsURL(!isURL)
  }

  const onIsHoverChange = () => {
    setIsHover(!isHover)
  }

  const onIsBorderChange = () => {
    setIsBorder(!isBorder)
  }


  return (
    <>
    <AppTitle>Create your own button!</AppTitle>
    <StyledApp>
      <Menu
        text={text}
        onTextChange={onTextChange}
        btnFontSize={btnFontSize}
        onFontSizeChange={onFontSizeChange}
        btnHeight={btnHeight}
        onHeightChange={onHeightChange}
        btnWidth={btnWidth}
        onWidthChange={onWidthChange}
        setBgColor={setBgColor}
        bgColor={bgColor}
        borderSize={borderSize}
        onBorderSizeChange={onBorderSizeChange}
        borderStyle={borderStyle}
        onBorderStyleChange={onBorderStyleChange}
        borderColor={borderColor}
        setBorderColor={setBorderColor}
        fontColor={fontColor}
        setFontColor={setFontColor}
        borderRadius={borderRadius}
        setBorderRadius={setBorderRadius}
        hoverColor={hoverColor}
        setHoverColor={setHoverColor}
        hoverBgColor={hoverBgColor}
        setHoverBgColor={setHoverBgColor}
        hoverBorderColor={hoverBorderColor}
        setHoverBorderColor={setHoverBorderColor}
        url={url}
        onUrlChange={onUrlChange}
        btnMargin={btnMargin}
        setBtnMargin={setBtnMargin}
        btnPadding={btnPadding}
        setBtnPadding={setBtnPadding}
        textAlign={textAlign}
        onTextAlignChange={onTextAlignChange}
        transitionTime={transitionTime}
        onTransitionTimeChange={onTransitionTimeChange}
        isURL={isURL}
        onIsURLChange={onIsURLChange}
        isHover={isHover}
        onIsHoverChange={onIsHoverChange}
        isBorder={isBorder}
        onIsBorderChange={onIsBorderChange}
        hoverBorderSize={hoverBorderSize}
        hoverBorderStyle={hoverBorderStyle}
        setHoverBorderSize={setHoverBorderSize}
        setHoverBorderStyle={setHoverBorderStyle}
      />
      <AppContainer>
        <ButtonContainer>
          <Button
            text={text}
            btnFontSize={btnFontSize}
            btnHeight={btnHeight}
            btnWidth={btnWidth}
            bgColor={bgColor}
            borderSize={borderSize}
            borderStyle={borderStyle}
            borderColor={borderColor}
            fontColor={fontColor}
            borderRadius={borderRadius}
            hoverColor={hoverColor}
            hoverBgColor={hoverBgColor}
            hoverBorderColor={hoverBorderColor}
            url={url}
            btnMargin={btnMargin}
            btnPadding={btnPadding}
            textAlign={textAlign}
            transitionTime={transitionTime}
            isURL={isURL}
            isHover={isHover}
            isBorder={isBorder}
            hoverBorderSize={hoverBorderSize}
            hoverBorderStyle={hoverBorderStyle}

          />
        </ButtonContainer>
        <Clipboard
          text={text}
          btnFontSize={btnFontSize}
          btnHeight={btnHeight}
          btnWidth={btnWidth}
          bgColor={bgColor}
          borderSize={borderSize}
          borderStyle={borderStyle}
          borderColor={borderColor}
          fontColor={fontColor}
          borderRadius={borderRadius}
          hoverColor={hoverColor}
          hoverBgColor={hoverBgColor}
          hoverBorderColor={hoverBorderColor}
          url={url}
          btnMargin={btnMargin}
          btnPadding={btnPadding}
          textAlign={textAlign}
          transitionTime={transitionTime}
          isURL={isURL}
          isHover={isHover}
          isBorder={isBorder}
          hoverBorderSize={hoverBorderSize}
          hoverBorderStyle={hoverBorderStyle}
        />
      </AppContainer>
    </StyledApp>
    </>
  );
}

export default App;
